* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: rgb(229, 103, 246);
}



.container {
  margin: 1rem;
  text-align: center;
  margin-top: 15%;
}

.nav {
  background-color: transparent;
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 0 1rem;
  margin-top: 10px;
  width: 1000px;
  height: 100px;
  margin-left: 16%;
  font-weight: 700;
  position: fixed;
  top: 0;
  z-index: 1000;
  font-size: larger;
}

.site-tittle {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li:hover {
  background-color: rgb(255, 255, 255);
}

.nav li.active {
  background-color: white;
}

.home {
  background-image: url('set.jpg');
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
}

.home-text {
  text-align: center;
  margin-top: 150px;
  color: white;
  font-size: 35px;
  opacity: 0;
  animation: textFadeIn 2s ease-in-out forwards;
  animation-delay: 1s;
}

.home h1,
.pricing h1,
.about h1 {
  color: white;
}

.home p,
.pricing p,
.about p {
  color: white;
}

.about {
  background-image: url('about.jpg');
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
  position: relative;
  transition: background-image 1s ease-in-out;
  padding-left: 300px;
}

.about-text {
  text-align: center;
  color: white;
  font-size: larger;
  z-index: 1;
  opacity: 0;
  animation: textFadeIn 2s ease-in-out forwards;
  animation-delay: 1s;
}

.contact {
  background-image: url('Contact.webp');
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
}

.contact-text {
  text-align: center;
  margin-top: 150px;
  color: white;
  font-size: 35px;
  opacity: 0;
  animation: textFadeIn 2s ease-in-out forwards;
  animation-delay: 1s;
}

.contact h1,
.contact p {
  color: white;
}

@keyframes backgroundChange {
  0% {
      background-image: url('set.jpg');
  }
  50% {
      background-image: url('about.jpg');
  }
}

.about {
  animation: backgroundChange 5s forwards;
}

@keyframes textFadeIn {
  0% {
      opacity: 0;
      transform: translateY(20px);
  }
  100% {
      opacity: 1;
      transform: translateY(0);
  }
}
